@font-face {
  font-family: "Drina";
  src: local("drina"), url("../fonts/drina.woff2") format("woff2"), url("../fonts/drina.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: local("raleway-black"), url("../fonts/raleway-black.woff2") format("woff2"), url("../fonts/raleway-black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: local("raleway-extrabold"), url("../fonts/raleway-extrabold.woff2") format("woff2"), url("../fonts/raleway-extrabold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: local("raleway-bold"), url("../fonts/raleway-bold.woff2") format("woff2"), url("../fonts/raleway-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Raleway";
  src: local("raleway-medium"), url("../fonts/raleway-medium.woff2") format("woff2"), url("../fonts/raleway-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

a {
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

@keyframes bounce {
  0% {
    transform: translateY(-2000px);
  }
  70% {
    transform: translateY(30px);
  }
  90% {
    transform: translateY(-10px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}
.page {
  height: 100%;
  scroll-behavior: smooth;
}

.page__body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 100%;
}

.container {
  width: 100%;
  max-width: 1215px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (max-width: 1023px) {
  .container {
    width: 100%;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 5px;
  }
}

.header {
  flex: 0 0 auto;
  min-height: 48px;
  background: linear-gradient(100.53deg, #E4F8F2 13.43%, #E0F3EE 26.9%, #CAF9EF 59.86%, #C4EAE2 92.69%);
}
@media (max-width: 767px) {
  .header {
    display: flex;
    align-items: center;
  }
}

.header__logo {
  margin-right: 43px;
}

.header__wrapper {
  display: flex;
  align-items: center;
  max-width: 1066px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding: 0 23px;
    position: relative;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 0;
  }
}
.header__wrapper img {
  margin-right: 29px;
}
@media (max-width: 1023px) {
  .header__wrapper img {
    margin-right: auto;
  }
}
@media (max-width: 767px) {
  .header__wrapper img {
    width: 79px;
    height: 36px;
  }
}
.header__wrapper button {
  display: none;
}
@media (max-width: 1023px) {
  .header__wrapper button {
    position: absolute;
    display: block;
    width: 34px;
    height: 34px;
    cursor: pointer;
    right: 34px;
  }
}
@media (max-width: 767px) {
  .header__wrapper button {
    width: 30px;
    height: 30px;
    right: 0;
  }
}

.header__nav {
  margin-right: auto;
  background: radial-gradient(51.16% 8231.72% at 50.07% 50%, #ffffff 0%, rgba(0, 0, 0, 0) 100%);
  position: relative;
}

.header__nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  height: 53px;
  align-items: center;
}
@media (max-width: 1023px) {
  .header__nav-list {
    transform: translateY(-100px);
    display: none;
  }
}

.header__nav-item {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 600;
  font-style: normal;
  margin-right: 48px;
}
.header__nav-item a {
  color: #0f656e;
  padding-bottom: 2px;
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
  transition: 0.3s ease-in-out;
}
.header__nav-item a:hover, .header__nav-item a:focus-visible {
  background-size: 100% 1px;
}
.header__nav-item a:active {
  opacity: 0.7;
}
.header__nav-item--active a {
  color: #ff6754;
}

.header__tel {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 12px;
  line-height: 14px;
  font-weight: 700;
  font-style: normal;
  padding-bottom: 2px;
  background-image: linear-gradient(currentColor, currentColor);
  background-position: 0% 100%;
  background-repeat: no-repeat;
  background-size: 0% 1px;
  transition: 0.3s ease-in-out;
  color: #0f656e;
  margin-right: 83px;
  position: relative;
}
.header__tel:hover, .header__tel:focus-visible {
  background-size: 100% 1px;
}
@media (max-width: 1023px) {
  .header__tel {
    margin-right: 88px;
  }
}
@media (max-width: 767px) {
  .header__tel {
    font-size: 9px;
    line-height: 11px;
    margin-right: 38px;
    margin-top: 5px;
  }
}
.header__tel::before {
  position: absolute;
  content: "";
  background-image: url("../img/icon/phone.svg");
  background-repeat: no-repeat;
  background-size: cover;
  width: 15px;
  height: 15px;
  left: -22px;
  top: -1px;
}
@media (max-width: 767px) {
  .header__tel::before {
    width: 10px;
    height: 10px;
    left: -17px;
    top: 0px;
  }
}

.main {
  flex: 1 0 auto;
  overflow: hidden;
  transition: 0.3s ease-in-out;
  background-color: #ffffff;
}
.main h2 {
  font-family: "Drina", "Comic Sans MS", sans-serif;
  font-size: 73px;
  line-height: 74px;
  font-weight: 400;
  font-style: normal;
  color: #08454e;
  margin: 0;
}
@media (max-width: 1023px) {
  .main h2 {
    font-size: 65px;
    line-height: 66px;
  }
}
@media (max-width: 767px) {
  .main h2 {
    font-size: 40px;
    line-height: 40px;
  }
}

.intro {
  min-height: 727px;
  background: linear-gradient(100.53deg, #E4F8F2 13.43%, #E0F3EE 26.9%, #CAF9EF 59.86%, #C4EAE2 92.69%);
  position: relative;
}
@media (max-width: 1023px) {
  .intro {
    min-height: 1028px;
  }
}
@media (max-width: 767px) {
  .intro {
    min-height: 587px;
    overflow: hidden;
  }
}
.intro::after, .intro::before {
  position: absolute;
  content: "";
}
.intro::after {
  width: 568px;
  height: 568px;
  background: #f8fbdf;
  filter: blur(240px);
  right: 24px;
  bottom: 0;
}
@media (max-width: 767px) {
  .intro::after {
    width: 328px;
    height: 328px;
  }
}
.intro::before {
  width: 328px;
  height: 328px;
  background: #ffffff;
  filter: blur(100px);
  bottom: 119px;
  right: 119px;
  z-index: 1;
}
@media (max-width: 767px) {
  .intro::before {
    width: 92px;
    height: 92px;
  }
}
.intro h1,
.intro p {
  margin: 0;
}
.intro h1 {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 52px;
  line-height: 61px;
  font-weight: 900;
  font-style: normal;
  padding-bottom: 33px;
  color: #0f656e;
}
@media (max-width: 1023px) {
  .intro h1 {
    font-size: 42px;
    line-height: 49px;
    padding-bottom: 19px;
  }
}
@media (max-width: 767px) {
  .intro h1 {
    font-size: 20px;
    line-height: 23px;
    letter-spacing: -0.1px;
    padding-bottom: 14px;
  }
}
.intro h1 span {
  font-family: "Drina", "Comic Sans MS", sans-serif;
  font-size: 100px;
  line-height: 125px;
  font-weight: 400;
  font-style: normal;
  color: #ff6754;
  padding-top: 10px;
}
@media (max-width: 1023px) {
  .intro h1 span {
    font-size: 80px;
    line-height: 103px;
  }
}
@media (max-width: 767px) {
  .intro h1 span {
    font-size: 40px;
    line-height: 49px;
  }
}
.intro p {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 24px;
  line-height: 28px;
  font-weight: 500;
  font-style: normal;
  width: 567px;
  letter-spacing: 0.3px;
  color: #08454e;
  width: calc(100% - 535px);
}
.intro p span {
  font-weight: 800;
}
@media (max-width: 1023px) {
  .intro p {
    text-align: center;
    max-width: 646px;
    width: 100%;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .intro p {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: -0.1px;
  }
}

.intro__wrapper {
  padding-top: 71px;
  margin-left: 70px;
  position: relative;
  z-index: 2;
}
@media (max-width: 1023px) {
  .intro__wrapper {
    margin-left: 0;
    text-align: center;
    padding-top: 54px;
  }
}
@media (max-width: 767px) {
  .intro__wrapper {
    padding-top: 9px;
  }
}

.intro__img {
  position: absolute;
  right: -28px;
  top: 120px;
  z-index: -1;
}
@media (max-width: 1023px) {
  .intro__img {
    position: relative;
    width: 517px;
    height: 444px;
    margin: 0 auto;
    top: 16px;
