.about__wrapper {
  margin-top: 136px;
  margin-left: 6px;
  margin-bottom: 48px;
  display: flex;
}
@media (max-width: 1023px) {
  .about__wrapper {
    flex-direction: column;
    margin: 87px 0 82px;
  }
  .about__wrapper img {
    width: 549px;
    margin: 0 auto;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    margin: 45px 0 40px;
  }
