}
@media (max-width: 1023px) {
  .about__desc {
    order: -1;
    max-width: 633px;
    width: 100%;
    text-align: center;
    margin: 0 auto 45px;
  }
}
@media (max-width: 767px) {
  .about__desc {
    margin-bottom: 24px;
  }
}
.about__desc h2 {
  padding-bottom: 50px;
}
@media (max-width: 1023px) {
  .about__desc h2 {
    padding-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .about__desc h2 {
    padding-bottom: 29px;
  }
}
.about__desc blockquote {
  margin: 0;
  padding: 0;
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 800;
  font-style: normal;
  color: #ff6754;
  letter-spacing: 0.1px;
  padding-bottom: 50px;
}
@media (max-width: 1023px) {
  .about__desc blockquote {
    padding-bottom: 44px;
  }
}
@media (max-width: 767px) {
  .about__desc blockquote {
    font-size: 12px;
    line-height: 15px;
    padding-bottom: 24px;
  }
}

.about__founder {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 900;
  font-style: normal;
  color: #ff6754;
  padding-bottom: 6px;
  margin: 0;
  letter-spacing: 0.1px;
}
@media (max-width: 767px) {
  .about__founder {
    font-size: 12px;
    line-height: 16px;
  }
}

.about__description {
  font-family: "Raleway", "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 500;
  font-style: normal;
  color: #000000;
  margin: 0;
  letter-spacing: 0.1px;
}
@media (max-width: 1023px) {
  .about__description {
    padding: 0 36px;
  }
}
@media (max-width: 767px) {
  .about__description {
    font-size: 12px;
    line-height: 14px;
    padding: 0 5px;
  }
}

.how-work {
  background: linear-gradient(100.53deg, #E4F8F2 13.43%, #E0F3EE 26.9%, #CAF9EF 59.86%, #C4EAE2 92.69%);
  min-height: 883px;
}
@media (max-width: 1023px) {
  .how-work {
    min-height: 868px;
  }
}

.how-work__wrapper {
  max-width: 830px;
  width: 100%;
  margin: 0 auto;
  padding: 98px 0 72px;
}
@media (max-width: 1023px) {
  .how-work__wrapper {
    text-align: center;
    padding-bottom: 67px;
  }
}
@media (max-width: 767px) {
  .how-work__wrapper {
    padding: 49px 0 59px;
  }
}
.how-work__wrapper h2 {
  max-width: 438px;
  width: 100%;
  margin: 0 auto;
  padding-bottom: 62px;
}
@media (max-width: 767px) {
  .how-work__wrapper h2 {
    padding-bottom: 22px;
  }
}

.how-work__features-list {
  margin: 0;
  padding: 0;
