}
.features__wrapper h2 {
  max-width: 508px;
  text-align: center;
  width: 100%;
}
@media (max-width: 1023px) {
  .features__wrapper h2 {
    margin: 0 auto;
  }
}

.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
