.callback {
  min-height: 358px;
  overflow: hidden;
  background: linear-gradient(100.53deg, #E4F8F2 13.43%, #E0F3EE 26.9%, #CAF9EF 59.86%, #C4EAE2 92.69%);
  position: relative;
}
@media (max-width: 1023px) {
  .callback {
    min-height: 380px;
  }
}
@media (max-width: 767px) {
  .callback {
    min-height: 339px;
  }
}
.callback::before {
  background-image: url("../img/callback-girl.webp");
  background-repeat: no-repeat;
  content: "";
  width: 352px;
  height: 474px;
  position: absolute;
  right: calc((100% - 886px) / 2);
  bottom: -148px;
  z-index: 3;
}
@supports not (background-image: url("../img/callback-girl.webp")) {
  .callback::before {
    background-image: url("../img/callback-girl.png");
